type ActionList = 'ADD_CONST' | 'MINUS_CONST' | 'OTHER'

interface Action {
  type: ActionList,
  payload?: object,
}

// appReducer.js
const initialState = { count: 0 };

const reducer = (state = initialState, action: Action) => {
  switch (action.type) {
    case 'ADD_CONST':
      console.log('add');
      return { count: state.count + 1 };
    case 'MINUS_CONST':
      console.log('minus');
      return { count: state.count - 1 };
    case 'OTHER':
      return {
        ...state,
        ...action.payload
      }
    default:
      return state;
  }
};
export default reducer;