import React from 'react'
import store from '../services/mobile/index'

const ReduxPages: React.FC = () => {
  const [count, setCount] = React.useState<number>(0)
  const addCount = () => {
    store.dispatch({
      type: 'ADD_CONST'
    })
    unSubscribe()
  }
  const minuseCount = () => {
    store.dispatch({
      type: 'MINUS_CONST'
    })
    unSubscribe()
  }
  const otherC = () => {
    store.dispatch({
      type: 'OTHER',
      payload: {
        name: 'cpp'
      }
    })
  }
  const unSubscribe = store.subscribe(() => {
    console.log('store', store.getState());
    setCount(store.getState().count)
  })

  return (
    <div>
      this is RRRRedux
      <h1>{count}</h1>
      <button onClick={addCount}> + 加</button>
      <button onClick={minuseCount}> - 减</button>
      <button onClick={otherC}> 其他信息 </button>
    </div>
  )
}
export default ReduxPages