import * as React from 'react'
import {themeContext} from './index'
const TTT = (props: any) => (
  <themeContext.Consumer>
    {
      value => (
          <h1>
            {value.name}---{props.children}
          </h1>
        )
    }
  </themeContext.Consumer>
)
export default TTT