import * as React from 'react';
import './App.css';
import routers, {IRouterProps} from './route'
import BasicLayout from './layout/basicLayout'
import {
  Route, Switch, Redirect, BrowserRouter
} from 'react-router-dom'

const App: React.FC = () => {
  const isLogin = true;
  return (
    <BrowserRouter basename={
      (window as any).__POWERED_BY_QIANKUN__ ? '/app-react' : '/'
    }>
      <Switch>
        {
          routers.map((item: IRouterProps, index: number) => {
            return (
              <Route
                key={index}
                exact
                path={item.path}
                render={
                  (props: any) => {
                    if (!item.isNeedLogin || isLogin) {
                      if(item.isNeedLogin) {
                        return (
                          <BasicLayout {...props}>
                            <item.component {...props}></item.component>
                          </BasicLayout>
                        )
                      } else {
                        return (
                          <item.component {...props}></item.component>
                        )
                      }
                    } else {
                      return (
                        <Redirect exact to={'/login'}></Redirect>
                      )
                    }
                  }
                }
              >
              </Route>
            )
          })
        }
      </Switch>
    </BrowserRouter>
  )
}
export default App;
